* {
    margin: 0;
    padding: 0;
    font-family: Gotham Pro;
}

.main-block-title{
    margin-left: 6.8vw;
}

body {
    background: #1B1E23;
}

/*    site header   */
.header-title {
    width: 100%;
    height: 10vh;

    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-content: center;
    align-items: center;

    margin-bottom: 4.28vh;
}

.head-logo {
    margin-left: -3.8vw;
    width: 7.5vw;

}

.buttons {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;

    cursor: pointer;

    margin-right: 2.9vw;
    white-space: nowrap;

    font-family: Gotham Pro;
    font-style: bold;
    font-size: 2vh;

}

.buttons>*:hover{
    opacity: 0.7;
}

.buttons>:first-child {
    color: #FFF700;

    margin-right: 3.8vw;
}

.buttons>:last-child {
    background-color: #FFF700;
    

    width: 7.3vw;
    height: 5.14vh;
    text-align: center;

    display: flex;
    align-items: center;
    justify-content: center;

    border-radius: 1vh;
}


.pagination{
    width: 20vw;

    position: absolute;

    bottom: 3.28vh;

    display: flex;

    flex-direction: row;
    justify-content: space-between;
    align-items: flex-start;
    z-index: 5;
}
.pagination>div{
    width: 3vw;
    height: 3vw;

    display: flex;
    justify-content: center;

    cursor: pointer;
    
}
.dot-pag{
    margin-top: 1vh;
}